.button {
  display: inline-block;
  vertical-align: top;
  font-family: @base-font-family;
  padding: 10px 20px;
  font-size: 1em;
  font-weight: 400;
  line-height: 1;
  text-align: center;
  text-decoration: none;
  color: @base-text-color !important;
  margin: 0;
  border: 0;
  box-shadow: none;
  cursor: pointer;
  background-color: @elements-bg-color-light;
  padding: 0.8em 2.5ex;
  outline: none;

  .animation-ease-in-quad(100ms);
  .beveled;

  &:hover {
    background-color: @elements-bg-color-light + 10;
  }

  &:active {
    background-color: @elements-bg-color-light + 17;
    .box-shadow (inset 0 2px 2px rgba(0, 0, 0, 0.2));
    .scale(0.985);
  }

  &:disabled {
    opacity: 0.7;

    &:active {
      .scale(1);
    }
  }

  &.flat {
    .box-shadow(none);
  }

  &.loading {
    animation-timing-function: linear;
    animation-name: button;
    animation-iteration-count: infinite;
    animation-duration: 400ms;
    background-repeat: repeat;
    background-position: 0 0;
    background-image: url('data:image/png;base64,');
  }
}

.button-fat {
  padding: 1.1em 2.5ex;
}

.button-slim {
  padding: 0.55em 2.5ex;
}

.button-skinny {
  padding: 0.4em 2.5ex;
}

.button-go {
  background-color: @color-green;
  color: #fff !important;

  &:hover {
    background-color: @color-green + 10;
  }

  &:active {
    background-color: @color-green + 17;
  }

  &:disabled {
    background-color: @color-green !important;
  }
}

.button-stop {
  background-color: @color-red;
  color: #fff !important;

  &:hover {
    background-color: @color-red + 10;
  }

  &:active {
    background-color: @color-red + 17;
  }

  &:disabled {
    background-color: @color-red !important;
  }
}

.button-attention {
  background-color: @color-yellow;
  color: #fff !important;

  &:hover {
    background-color: @color-yellow + 10;
  }

  &:active {
    background-color: @color-yellow + 17;
  }

  &:disabled {
    background-color: @color-yellow !important;
  }
}

.button-action {
  background-color: @color-blue;
  color: #fff !important;

  &:hover {
    background-color: @color-blue + 10;
  }

  &:active {
    background-color: @color-blue + 17;
  }

  &:disabled {
    background-color: @color-blue !important;
  }
}